<template>
  <div id="main" style="width: 100%; height: 800px"></div>
</template>

<script>
import * as echarts from 'echarts';
import "@/js/ChinaMapchain.js";

export default {
  data() {
    return {
      tableData: [] // JSON
    };
  },
  created() {
    //  created  fetch 
    fetch("/assets/json/ChinaMapPoint.json")
      .then(response => response.json())
      .then(data => {
        //  tableData 
        this.tableData = data.map(item => ({
          name: item.name,
          value: item.value,
          sales: item.Sales
        }));

        //  tableData 
        console.log("tableData:", this.tableData);

        //  DOM 
        this.$nextTick(() => {
          this.initChart();
        });
      })
      .catch(error => {
        console.error("Error fetching JSON data:", error);
      });
  },
  methods: {
    initChart() {
      //  ECharts 
      const myChart = echarts.init(document.getElementById('main'));

      //  dealWithData  tableData 
      this.dealWithData(this.tableData, myChart);
    },
    dealWithData(data, chart) {
      // 
      let dataValue = data;
      const option = {
        tooltip: {
          show: true,
          triggerOn: 'click'
        },
        geo: {
          map: 'china',
          roam: false,
          zoom: 1.2,
          center: [105, 36],
          itemStyle: {
            areaColor: '#f5f2f2',
            borderColor: "#835f5f",
          },
          label: {
            show: true,
            fontSize: "11.5",
            color: "rgb(107,102,102)"
          },
          emphasis: {
            itemStyle: {
              areaColor: '#d0a3a3',
            },
            label: {
              show: true,
              color: "rgb(255,255,255)"
            }
          }
        },
        series: [
          {
            name: '',
            type: 'scatter',
            coordinateSystem: 'geo',
            data: dataValue,
            symbolSize: 12,
            itemStyle: {
              color: "#f13434",
              shadowBlur: 2,
              shadowColor: "#333"
            },
            tooltip: {
              show: true,
              triggerOn: "click",
              formatter: function(data1) {
                let data2 = data1.data;
                return data2.sales;
              }
            },
          },
          {
            name: "",
            type: "effectScatter",
            coordinateSystem: "geo",
            data: dataValue,
            symbolSize: 4,
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke",
              color: "#f13434",
              period: 10,
              scale: 10
            },
            emphasis: {
              scale: true,
            },
            label: {
              formatter: "{b}",
              position: "top",
              show: true,
              fontSize: "10",
            },
            itemStyle: {
              color: "#f13434",
              shadowBlur: 2,
              shadowColor: "#333"
            },
            tooltip: {
              show: true,
              triggerOn: "click",
              formatter: function(data1) {
                let data2 = data1.data;
                return data2.sales;
              }
            },
            zlevel: 1
          }
        ]
      };

      // 
      chart.setOption(option);
    }
  }
};
</script>
