<template>
  <div>
    <div class="heading"></div>
    <hr class="heading-hr" />

    <div class="practice">
      <div class="images">
        <img src="@/assets/images/CUMT-view1.jpg" alt="Image 1" />
        <img src="@/assets/images/CUMT-view4.jpg" alt="Image 2" />
      </div>
      <div class="stats">
        <div
          class="number-container"
          style="border: 2px solid rgb(30, 50, 100)"
        >
          <p>
            
            211985
            
            
            
          
          </p>
        </div>
        <div
          class="number-container"
          v-for="(stat, index) in stats"
          :key="index"
          @click="increment(stat)"
        >
          <span class="description">{{ stat.description }}</span>
          <span class="number-display">{{ stat.displayNumber }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      stats: [
        { description: '', end: 86, displayNumber: '???' },
        { description: '', end: 50, displayNumber: '???' },
        { description: '', end: 97, displayNumber: '???' }
      ]
    };
  },
  methods: {
    increment(stat) {
      let start = 1;
      const end = stat.end;
      stat.displayNumber = ''; // 
      const interval = setInterval(() => {
        start += 2;
        if (start > end) {
          clearInterval(interval);
          stat.displayNumber = end.toLocaleString();
        } else {
          stat.displayNumber = start.toLocaleString();
        }
      }, 10);
    }
  }
};
</script>

