<template>
  <div class="messages">
    <div class="heading"></div>
    <hr class="heading-hr" />

    <div class="form">
      <input v-model="username" type="text" placeholder="" />
      <textarea v-model="message" placeholder=""></textarea>
      <button @click="submitMessage" class="btn"></button>
    </div>

    <div id="messageBoard">
      <div v-for="(msg, index) in messages" :key="index" class="message">
        <div class="message-info">
          <div class="info">
            <img :src="require(`@/assets/images/${msg.avatar}`)" alt="" />
            <strong class="name">{{ msg.username }}</strong>
          </div>
          <span>{{ msg.timestamp }}</span>
        </div>
        <div class="content">
          {{ msg.message }}
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      message: '',
      messages: [
        {
          username: '',
          message: '',
          avatar: 'mouse.jpg',
          timestamp: '2024/08/21 12:00:00'
        },
        {
          username: '',
          message: '',
          avatar: 'mouse.jpg',
          timestamp: '2024/08/21 12:05:00'
        },
        {
          username: '',
          message: '211985  42001562000 218177 1823620 2374236001330068010000 3450',
          avatar: 'mouse.jpg',
          timestamp: '2024/08/21 12:10:00'
        }
      ]
    };
  },
  methods: {
    submitMessage() {
      if (this.message.trim() === '') {
        alert('');
        return;
      }

      const user = this.username.trim() === '' ? '' : this.username;
      const timestamp = this.getCurrentTime();
      const defaultAvatar = 'mouse.jpg'; // 

      //  messages 
      this.messages.unshift({
        username: user,
        message: this.message,
        avatar: defaultAvatar,
        timestamp
      });

      // 
      this.username = '';
      this.message = '';
    },
    getCurrentTime() {
      const now = new Date();
      const year = now.getFullYear();
      const month = ('0' + (now.getMonth() + 1)).slice(-2);
      const day = ('0' + now.getDate()).slice(-2);
      const hours = ('0' + now.getHours()).slice(-2);
      const minutes = ('0' + now.getMinutes()).slice(-2);
      const seconds = ('0' + now.getSeconds()).slice(-2);
      return `${year}/${month}/${day} ${hours}:${minutes}:${seconds}`;
    }
  }
};
</script>

