  font-size: 25px;
  font-family: ;
}

input,
textarea {
  display: block;
  margin: 10px auto;
  padding: 10px;
  border-radius: 5px;
  border: 2px solid rgb(30, 50, 100);
  background-color: #fff;
  color: #333;
  font-size: 16px;
  width: 80%;
  box-sizing: border-box;
}

textarea {
  resize: none;
}

input::placeholder,
textarea::placeholder {
  color: #aaa;
}

.btn {
  padding: 10px 20px;
  font-size: 16px;
  background-color: rgb(30,50,100);
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  margin-left: 10px;
  box-sizing: border-box;
  height: 40px;
  line-height: 20px;
}

.btn:hover {
  color: #ff9900;
}

.message {
  width: 80%; /*  */
  margin: 10px auto; /*  */
  background-color: #f9f9f9;
  border-radius: 5px;
  border: 2px solid rgb(30, 50, 100);
  box-sizing: border-box;
  padding: 10px; /*  */
}

.message-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px; /*  */
  font-family: ;
  margin-right: 10px
}

.message-info img {
  width: 50px;
